@import "./default/default.scss";
.body-banner {
  background: rgba(0, 0, 0, 0.45);
  position: relative;
  height: 600px;
  &.video {
    cursor: pointer;
    &:hover {
      background: rgba(0, 0, 0, 0.55);
    }
  }
  @media (max-width: $tablet) {
    max-height: 450px;
  }
  @media (max-width: $mobile) {
    max-height: 250px;
  }
  overflow: hidden;
  img {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
  }
  .banner-container {
    margin-top: 195px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    height: 100%;
    gap: 28px;
    @media (max-width: $tablet) {
      margin-top: auto;
      justify-content: center;
    }
    @media (max-width: $mobile) {
      gap: 11px;
    }
    &__title {
      @include rale-800-62;
      color: $white;
      @media (max-width: $mobile) {
        margin-top: 17px;
        margin-top: 0;
      }
    }
    &__description {
      max-width: 550px;
      @include rale-600-25;
      line-height: 32px;
      margin-top: 2px;
      margin-right: 1px;
      color: $white;
      text-align: center;
      @media (max-width: $mobile) {
        margin-top: 0;
        max-width: 300px;
        line-height: 23px;
        letter-spacing: 0.01em;
      }
    }
    .icon-play {
      margin-top: 10px;
      color: $white;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 61px;
      height: 61px;
      border: 2px solid $white;
      border-radius: 50%;
      font-size: 17px;
      &::before {
        margin-left: 5px;
      }
      @media (max-width: $mobile) {
        width: 45px;
        height: 45px;
        &::before {
          font-size: 13px;
        }
      }
    }
  }
  img {
  }
}

.page-banner {
  position: relative;
  height: 100%;
  overflow: hidden;
  &::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba($color: #000000, $alpha: 0.45);
  }
  &.video {
    cursor: pointer;
    &:hover {
      background-color: rgba(0, 0, 0, 0.329);
    }
  }
  &_litle {
    height: 300px;
    .banner-container {
      margin-top: 4px;
      &__title {
        @include rale-800-62;
        color: $white;
        text-align: center;
        @media (max-width: $mobile) {
          margin-top: 17px;
          margin-top: 0;
        }
      }
    }
    @media (max-width: $mobile) {
      height: 200px;
    }
  }
  &_medium {
    height: 600px;
    @media (max-width: $tablet) {
      height: 400px;
    }
    @media (max-width: $mobile) {
      height: 300px;
    }
    .banner-container {
      margin-top: 27px;
      &__title {
        @include rale-800-62;
        color: $white;
        text-align: center;
        @media (max-width: $mobile) {
          margin-top: 17px;
          margin-top: 0;
        }
      }
      &__description {
        max-width: 550px;
        @include rale-600-25;
        line-height: 32px;
        margin-top: 30px;
        margin-right: 1px;
        color: $white;
        text-align: center;
        @media (max-width: $mobile) {
          margin-top: 10px;
          max-width: 300px;
          line-height: 23px;
          letter-spacing: 0.01em;
        }
      }
    }
  }
  &_big {
    height: 740px;
    .banner-container {
      &__icon-eco {
        margin-top: 10px;
        color: $white;
        margin-left: 1px;
        font-size: 74px;
      }
      &__title {
        margin-top: 45px;
        margin-right: 17px;
        @include rale-800-45;
        text-align: center;
        max-width: 1100px;
      }
      &__description {
        margin-top: 20px;
        margin-left: 6px;
        @include rale-500-22;
        max-width: 700px;
        line-height: 30px;
        text-align: center;
      }
    }
    @media (max-width: $tablet) {
      height: 500px;
    }
    @media (max-width: $mobile) {
      height: 350px;
    }
  }
  img {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
  }
  .banner-container {
    position: relative;
    z-index: 1;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: $white;
    .icon-play {
      margin-top: 40px;
      color: $white;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 61px;
      height: 61px;
      border: 2px solid $white;
      border-radius: 50%;
      font-size: 17px;
      &::before {
        margin-left: 5px;
      }
      @media (max-width: $mobile) {
        margin-top: 20px;
        width: 45px;
        height: 45px;
        &::before {
          font-size: 13px;
        }
      }
    }
  }
}
